
  /* =Photos
  ----------------------------------------------- */
  .photos {
    margin: 1.5em -1.5em -3.75em;
  }
  @media (min-width: 60em) {
    .photos {
      margin: 3em calc(-50vw + 20em) -6em;
    }
  }
  .photos ul,
  .photos li {
    /* @reset-list */
    list-style: none;
    margin-left: 0;
    padding: 0;
    border-width: 0;
    font-style: inherit;
  }
  .photos ul,
  .photos li {
    max-width: none;
  }
  .photos::after {
    content: "";
    display: table;
    clear: both;
  }
  .photos img {
    display: block;
    width: 100%;
  }
  @media (min-width: 70em) {
    .photos img {
      width: 50%;
      float: left;
    }
    .photos li:nth-child(2n+1) img {
      clear: both;
    }
    .photos li:nth-child(2n+1):last-child {
      width: 100%;
      float: none;
    }
  }
